#top-bars {
  position: fixed;
  top: 0;
  width: 100%;
  height: 11vw;
}
#top-bars .bar {
  lost-column: 1/4 4 0;
  transition: height 0.6s ease-in-out;
}
#top-bars .bar:nth-child(1) {
  height: 90%;
  background: #009bdb;
}
#top-bars .bar:nth-child(2) {
  height: 70%;
  background: #ba007c;
}
#top-bars .bar:nth-child(3) {
  height: 80%;
  background: #feee00;
}
#top-bars .bar:nth-child(4) {
  height: 100%;
  background: #000;
}
#jumbo {
  margin-top: 12vw;
}
@media (min-width: 740px) {
  #jumbo {
    height: 44vw;
  }
}
#jumbo .content {
  width: 95%;
  height: 100%;
  margin: 0 auto;
}
@media (max-width: 980px) {
  #jumbo .content {
    font-size: 0.7em;
  }
}
#jumbo .content .logo {
  text-align: center;
}
@media (max-width: 739px) {
  #jumbo .content .logo img {
    max-width: 400px;
  }
}
@media (min-width: 740px) {
  #jumbo .content .logo {
    lost-column: 1/3;
    lost-align: center;
    height: 100%;
  }
}
#jumbo .content .logo > div {
  width: 100%;
}
#jumbo .content .info {
  text-align: center;
}
@media (min-width: 740px) {
  #jumbo .content .info {
    lost-column: 2/3;
    lost-align: vertical;
    height: 100%;
  }
}
#jumbo .content .info .info-content {
  width: 100%;
}
#jumbo .content .info .images {
  margin-bottom: 10px;
  lost-utility: clearfix;
}
@media (max-width: 739px) {
  #jumbo .content .info .images {
    display: none;
    margin: 0;
  }
}
#jumbo .content .info .images .image {
  lost-column: 1/2 2;
  background: #808080;
  height: 150px;
}
#jumbo .content .info .contact .phone {
  display: inline-block;
  font-size: 2em;
  padding-left: 1.5em;
  margin-right: 1em;
  font-weight: bold;
