#jumbo .content .info .btn {
  display: inline-block;
  font-size: 1.5em;
  padding: 10px 20px;
  color: #fff;
  text-decoration: none;
  margin: 10px;
  background: #009bdb;
  transition: all 0.3s ease-in-out;
  border: 4px solid color(#009bdb b(30%));
}
#jumbo .content .info .btn:hover {
  background: color(#009bdb w(30%));
}
#jumbo .content .info .btn:active {
  transform: scale(0.9);
}
#jumbo .content .info .btn + .btn {
  background: #ba007c;
  transition: all 0.3s ease-in-out;
  border: 4px solid color(#ba007c b(30%));
}
#jumbo .content .info .btn + .btn:hover {
  background: color(#ba007c w(30%));
}
#jumbo .content .info .btn + .btn:active {
  transform: scale(0.9);
}
@media (max-width: 980px) {
  #jumbo .content .info .btn {
    padding: 5px 10px;
  }
}
#servicos {
  border-left: 10px solid #009bdb;
  padding-left: 10px;
  lost-utility: clearfix;
}
#servicos > h1 {
  padding: 10px 0 10px 20px;
  text-shadow: 0 1px 0 #fff;
  margin-top: 0;
  margin-left: -10px;
}
#contatos {
  border-left: 10px solid #ba007c;
  padding-left: 10px;
  lost-utility: clearfix;
}
#contatos > h1 {
  padding: 10px 0 10px 20px;
  text-shadow: 0 1px 0 #fff;
  margin-top: 0;
  margin-left: -10px;
}
